import React, { Component, memo } from 'react';
import ReactPlayer from "react-player"
import { CloudHostUrl } from '../../../constants';
import PostContent from '../../Themes/Elements/PostContent/PostContent';
import AuthorDetails from './Elements/AuthorDetails';
import './scss/spotlight.scss'
import { GET_IMAGE_SIZE } from '../../../utils/Common';
import { setIntervalFunction } from '../../../utils/display';
import debounce from 'lodash/debounce';

const mediaReplaceText = (mediaUrl, networkId) => {
    if (networkId === 25) return String(mediaUrl).replace("https://w.soundcloud.com/player/?url=", "");
    return mediaUrl;
}
class Spotlight extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imageError: false,
            windowWidth: window.innerWidth,
            windowHeight: window.innerHeight,
            mediaSize: { height: 400, width: 400 },
            animate: false,
            itemData: null,
            isPlaying: false
        };
    }
    componentDidMount() {
        this.onLoadAnimation()
    }
    handlePlay = () => {
        this.setState({ isPlaying: true });
    };

    componentDidUpdate(prevProps) {
        if (!debounce.isEqual(prevProps.itemData, this.props.itemData)) {
            this.setState({
                itemData: this.props.itemData,
                animate: false,
            })
            this.onLoadAnimation()
        }
    }
    onLoadAnimation() {
        setTimeout(() => {
            this.setState({
                animate: true,
            })
            this.onMediaLoad(this.props.itemData);
        }, 1000);
    }

    onMediaLoad = async (modalData) => {
        if (modalData.mediaHeight && modalData.mediaWidth) {
            this.setState({ mediaSize: { height: modalData.mediaHeight, width: modalData.mediaWidth } })
        } else {
            try {
                const { width, height } = await GET_IMAGE_SIZE(modalData.postFile);
                this.setState({ mediaSize: { height: height, width: width } })
            } catch (error) {
                this.setState({ mediaSize: { height: 100, width: 100 } })
            }
        }
    }
    render() {
        const { itemData, wall } = this.props
        const { imageError, windowHeight, windowWidth, mediaSize, animate, isPlaying } = this.state;
        const lineClamp = windowWidth > windowHeight ? ((windowHeight - 400) / 50).toFixed() : (((windowHeight / 2) - 200) / 50).toFixed()
        const mediaType = itemData.type !== 1 ? true : false;
        const isVideo = itemData.type === 3 || itemData.type === 5 ? true : false
        let mediaFile = `${itemData.postFile}`;

        const isMediaFile = !imageError && itemData.mediaFile != "" ? true : false;
        const onlyMedia = isMediaFile && (itemData.type != 1 || itemData.type != 4) ? true : false
        var lineClamp_ = onlyMedia ? lineClamp : lineClamp * 2

        const scale = Math.min(windowWidth / mediaSize.width, windowHeight / mediaSize.height);
        const height = mediaSize.height * scale
        const width = mediaSize.width * scale
        const paddingBottom = { paddingBottom: `${(height * 100) / width}%` }

        if (itemData.mediaFile.includes(CloudHostUrl)) mediaFile = itemData.postFile;
        return <div id="spotlight__" className="sl_container">
            <div className={`sl_overlay_ ${animate ? 'sl_media_ani' : ''}`} style={{ backgroundImage: `url(${mediaFile})` }}></div>
            <div className="sl_wrap">
                <div className={`sl_wrap_row sl_only_text`}>
                    {mediaType ?
                        <div className={`sl_media_wrap ${animate ? 'sl_media_ani' : ''}`} style={{ maxWidth: width }}>
                            {isVideo ?
                                <div className="sl_media_wrap_" style={paddingBottom}>
                                    {!isPlaying && (
                                        <img
                                            src={itemData.postFile}
                                            alt="Video Poster"
                                            style={{
                                                position: "absolute",
                                                top: 0,
                                                left: 0,
                                                width: "100%",
                                                height: "100%",
                                                objectFit: "cover",
                                                cursor: "pointer",
                                                zIndex: 1,
                                            }}
                                            onLoad={this.handlePlay}
                                        />
                                    )}
                                    <ReactPlayer
                                        url={mediaReplaceText(itemData.mediaUrl, itemData.networkId)}
                                        config={{
                                            facebook: {
                                                appId: '117029975354072',
                                                attributes: {
                                                    'data-width': 'auto',
                                                    muted: true,
                                                    poster: itemData.postFile
                                                }
                                            },
                                            file: {
                                                forceVideo: true,

                                                attributes: {
                                                    muted: true,
                                                    poster: itemData.postFile
                                                }
                                            }
                                        }}
                                        className="sl_media_"
                                        loop={true}
                                        autoPlay={true}
                                        muted={true}
                                        volume={1}
                                        playsinline={true}
                                        playing={true}
                                        height={'100%'}
                                        width={'100%'}
                                        controls={false}
                                    />
                                </div>
                                :
                                <div className="sl_media_wrap_" style={paddingBottom}>
                                    <img className="sl_media_" src={itemData.postFile} height={height} width={width} />
                                </div>
                            }
                        </div>
                        : null}

                    {/* <div className="sl_content_wrap" ref={this.contentRef}>
                            <div className="sl_content_in">
                                <PostContent contentClass={`sl_content sl_con_line-${lineClamp_}`} item={itemData} content={itemData.content} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} />
                            </div>
                        </div> */}

                    {/* <div className='sl_content'>
                            <AuthorDetails mediaType={personalization.mediaType} itemData={itemData.id} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                    </div> */}

                </div>
            </div>
        </div>
    }
}

export default memo(Spotlight)